.container {
    max-width: 1140px;
    margin: 0 auto;
}
.header {
    background: url(../../../img/backgrounds/main-bg.jpg) center center/cover no-repeat;
    max-height: 680px;
    padding: 20px 0 82px 0;
    &__navigation {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__menu {
        display: flex;
        align-items: center;
        list-style-type: none;
        font-size: 15px;
        padding-left: 0;
        margin-left: 59px;
        .header__link {
            margin: 0 5px;
            border-right: 2px dotted #858891;
            padding-right: 10px;
            &:last-of-type {
                border: none;
            }
        }
            a {
                color: #fff;
                transition: .3s all;
                &:hover {
                    text-decoration: none;
                    color: gray;
            }
        }
    }
    &__contacts {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: end;
        margin-left: 46px;
    }
    &__question {
        font-weight: 300;
        font-size: 14px;
        color: #fff;
        padding-left: 68px;
    }
    &__phone {
        font-weight: 700;
        font-size: 19px;
        color: #fff;
        &:hover {
            color: #fff;
            text-decoration: none;
        }
    }
    &__button {
        font-size: 12px;
        margin-left: 19px;
    }
}
.main {
    color: #fff;
    &__descr {
        text-align: center;
    }
    &__title {
        font-size: 24px;
        font-weight: 300;
    }
    &__header {
        font-size: 72px;
        color: $main_color;
        margin-top: 20px;
        line-height: 64px;
    }
    &__subheader {
        font-size: 42px;
        font-weight: 400;
        margin-top: 18px;
        line-height: 32px;
    }
    &__divider {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 38px;
        hr {
            width: 388px;
            color: #fff;
            margin: 0;
        }
        span {
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 4px;
            background-color: #fff;
            margin: 0 11px;
        }
    }
    &__icons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 30px;
    }
    &__item {
        text-align: center;
        width: 250px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-weight: 300;
        img {
            margin-bottom: 20px;
        }
    }
    &__button {
        display: block;
        margin: 0 auto;
        margin-top: 42px;
    }
}