@import "base/variables";
@import "../blocks/modules/modules";
@import "base/bootstrap-reboot.min";
* {
    font-family: 'Roboto', sans-serif;
}
h1, h2 {
    font-family: 'Roboto Condensed', sans-serif;
    margin-bottom: 0;
}
.btn {
    width: 160px;
    height: 45px;
    color: #fff;
    background-color: $main_color; 
    border: none;
    border-radius: 16px;
    text-transform: uppercase;
    transition: .3s all;
    &:hover {
        box-shadow: 3px 3px 20px $main_color;
    }
    &_big {
        width: 230px;
        height: 57px;
        border-radius: 20px;
    }
}
.title {
    font-size: 26px;
    font-weight: 700;
    color: #2e2e2e;
    text-transform: uppercase;
    text-align: center;
}
.title__orange {
    color: $main_color;
    font-weight: 700;
    font-size: 14px;
}