.container {
  max-width: 1140px;
  margin: 0 auto;
}

.header {
  background: url(../../../img/backgrounds/main-bg.jpg) center center/cover no-repeat;
  max-height: 680px;
  padding: 20px 0 82px 0;
}

.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu {
  display: flex;
  align-items: center;
  list-style-type: none;
  font-size: 15px;
  padding-left: 0;
  margin-left: 59px;
}

.header__menu .header__link {
  margin: 0 5px;
  border-right: 2px dotted #858891;
  padding-right: 10px;
}

.header__menu .header__link:last-of-type {
  border: none;
}

.header__menu a {
  color: #fff;
  transition: .3s all;
}

.header__menu a:hover {
  text-decoration: none;
  color: gray;
}

.header__contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: end;
  margin-left: 46px;
}

.header__question {
  font-weight: 300;
  font-size: 14px;
  color: #fff;
  padding-left: 68px;
}

.header__phone {
  font-weight: 700;
  font-size: 19px;
  color: #fff;
}

.header__phone:hover {
  color: #fff;
  text-decoration: none;
}

.header__button {
  font-size: 12px;
  margin-left: 19px;
}

.main {
  color: #fff;
}

.main__descr {
  text-align: center;
}

.main__title {
  font-size: 24px;
  font-weight: 300;
}

.main__header {
  font-size: 72px;
  color: #ec644b;
  margin-top: 20px;
  line-height: 64px;
}

.main__subheader {
  font-size: 42px;
  font-weight: 400;
  margin-top: 18px;
  line-height: 32px;
}

.main__divider {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 38px;
}

.main__divider hr {
  width: 388px;
  color: #fff;
  margin: 0;
}

.main__divider span {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 4px;
  background-color: #fff;
  margin: 0 11px;
}

.main__icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
}

.main__item {
  text-align: center;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-weight: 300;
}

.main__item img {
  margin-bottom: 20px;
}

.main__button {
  display: block;
  margin: 0 auto;
  margin-top: 42px;
}

.advantages {
  max-height: 590px;
  padding: 57px 0px 95px 0;
}

.advantages__wrapper {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 32px;
}

.advantages__item {
  display: flex;
  justify-content: start;
  align-items: start;
  width: 520px;
  margin-bottom: 35px;
}

.advantages__img {
  margin-right: 25px;
}

.advantages__text {
  font-weight: 300;
  font-size: 14px;
  margin-top: 7px;
  color: #3a3a3a;
}

