* {
  font-family: 'Roboto', sans-serif;
}

h1,
h2 {
  font-family: 'Roboto Condensed', sans-serif;
  margin-bottom: 0;
}

.btn {
  width: 160px;
  height: 45px;
  color: #fff;
  background-color: #ec644b;
  border: none;
  border-radius: 16px;
  text-transform: uppercase;
  transition: .3s all;
}

.btn:hover {
  box-shadow: 3px 3px 20px #ec644b;
}

.btn_big {
  width: 230px;
  height: 57px;
  border-radius: 20px;
}

.title {
  font-size: 26px;
  font-weight: 700;
  color: #2e2e2e;
  text-transform: uppercase;
  text-align: center;
}

.title__orange {
  color: #ec644b;
  font-weight: 700;
  font-size: 14px;
}