.advantages {
    max-height: 590px;
    padding: 57px 0px 95px 0;
    &__wrapper {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-top: 32px;
    }
    &__item {
        display: flex;
        justify-content: start;
        align-items: start;
        width: 520px;
        margin-bottom: 35px;
    }
    &__img {
        margin-right: 25px;
    }
    &__text {
        font-weight: 300;
        font-size: 14px;
        margin-top: 7px;
        color: #3a3a3a;
    }
}